import React from 'react'
import {BrowserRouter as Router,Route} from 'react-router-dom'
import './index.css'
import {Container} from 'react-bootstrap'
import Header from './components/Header'
import Footer from './components/Footer'
import Home from './pages/Home'
import ProductDetails from './pages/ProductDetails'
import Cart from './pages/Cart'
import Register from './pages/Register'
import Login from './pages/Login'
import Profile from './pages/Profile'
import ShippingAdress from './pages/ShippingAdress'
import Payment from './pages/Payment'
import PlaceOrder from './pages/PlaceOrder'
import OrderPage from './pages/OrderPage'

 
function App() {
    
    return (
        <Router>
            <Header/>
            
            <Container>
               <Route path="https://mern-shopzilla.herokuapp.com/" exact component={Home}/>
               <Route path="https://mern-shopzilla.herokuapp.com/register" component={Register}/>
               <Route path="https://mern-shopzilla.herokuapp.com/profile" component={Profile}/>
               <Route path="https://mern-shopzilla.herokuapp.com/login" component={Login}/>
               <Route path="https://mern-shopzilla.herokuapp.com/product/:id" component={ProductDetails}/>
               <Route path="https://mern-shopzilla.herokuapp.com/cart/:id?" component={Cart}/>
               <Route path="https://mern-shopzilla.herokuapp.com/shipping" component={ShippingAdress}/>
               <Route path="https://mern-shopzilla.herokuapp.com/payment" component={Payment}/>
               <Route path="https://mern-shopzilla.herokuapp.com/checkout" component={PlaceOrder}/>
               <Route path="https://mern-shopzilla.herokuapp.com/order/:id" component={OrderPage}/>
            </Container>
                       
            
            <Footer/>
        </Router>
    )
}

export default App
